.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

body{
  padding: 0;
  margin: 0;
}

.board{
  width: 500px;
  height: 500px;
  background-color: rgb(152, 85, 214);
  border: 1px solid black;
  display: flex;
  flex-direction: column;

}
.row{
  flex: 33%;
  display: flex;
  /* border: 1px solid black; */
}

.square{
  flex: 33%;
  height: 100%;
  border: 1px solid black;
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: 30px ;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}
.square:active{
  background-color: blueviolet;
}

@media  screen and (max-width: 400px) {
  .board{
    width: 15rem;
    height: 20rem;
    background-color: rgb(152, 85, 214);
   
  
  }
}